<template>
  <Page type="white" :padding="false" :showFooter="false">
    <weui-form
      title="模拟选择框"
      desc="用于丰富原生选择框结构，使其更具有扩展性"
    >
      <weui-form-group>
        <weui-select-group
          :isPicker="true"
          :placement="'after'"
          :showHeader="false"
          @clickBody="showDate()"
        >
          <template #body>{{ date }}</template>
        </weui-select-group>
        <weui-select-group
          :label="area.label"
          :isPicker="true"
          @clickHeader="showArea()"
        >
          <template #body>
            <input
              class="weui-input"
              type="number"
              pattern="[0-9]"
              placeholder="请输入号码"
              v-model="mobile"
            />
          </template>
        </weui-select-group>
        <weui-select-group
          label="票种"
          :isPicker="true"
          placement="after"
          @clickBody="showTraffic()"
        >
          <template #body>
            {{ traffic.label }}
          </template>
        </weui-select-group>
      </weui-form-group>
    </weui-form>
  </Page>
</template>
<script>
import Page from '@/components/Page.vue'

export default {
  data () {
    return {
      date: '日期',
      area: { label: '+86', value: '1' },
      mobile: '12345678907',
      traffic: { label: '的士票', value: '2' }
    }
  },
  components: { Page },
  methods: {
    showDate: function () {
      const config = {
        title: '多列选择器',
        start: '2000-01-01',
        end: '2026-12-12',
        default: [2000, 12, 9]
      }
      this.$picker.showDate(config).then((res) => {
        this.date = res
      })
    },
    showArea: function () {
      const config = {
        options: [
          { label: '+86', value: '1' },
          { label: '+80', value: '2' },
          { label: '+84', value: '3' },
          { label: '+87', value: '4' },
        ],
        default: 0
      }
      this.$picker.show(config).then((res) => {
        this.area = res[0]
      })
    },
    showTraffic: function () {
      const config = {
        options: [
          { label: '飞机票', value: '1', disabled: false },
          { label: '火车票', value: '2', disabled: false },
          { label: '的士票', value: '3', disabled: false },
          { label: '公交票(disabled)', value: '4', disabled: true },
          { label: '其他(disabled)', value: '5', disabled: true },
          { label: '好的(disabled)', value: '6', disabled: true },
        ],
        default: [2]
      }
      this.$picker.show(config).then((res) => {
        this.traffic = res[0]
      })
    }
  }
}
</script>
<style lang="scss" scoped>
/deep/ .weui-label {
  width: 3.1em;
}
</style>